import React, { useCallback, useEffect, useState } from "react";
import useFetch from "../../Hooks/Fetch";
import { useSettings } from "../context/useSettings";

const CluedoStart = ({ onStart }) => {
  const { settings } = useSettings();
  const [existingGameKey, setExistingGameKey] = useState();
  const { response: gameKey, doFetch } = useFetch();
  const [mode, setMode] = useState("existing");

  const handleExistingGame = () => {
    onStart(existingGameKey);
  };

  const handleInputChange = useCallback((e) => {
    setExistingGameKey(e.target.value);
  }, []);

  const handleModeChange = useCallback((e) => {
    setMode(e.target.value);
  }, []);

  const handleNewGame = () => {
    doFetch(settings.url.new, {
      method: "GET",
      auth: {
        user: settings.auth.username,
        password: settings.auth.password,
      },
    });
  };

  useEffect(() => {
    if (gameKey.data && gameKey.data.key) {
      onStart(gameKey.data.key);
    }
  }, [gameKey, onStart]);

  return (
    <div className={"file full"}>
      <h2>Cluedo</h2>

      <div style={{ margin: "20px 0" }}>
        <select name="mode" onChange={handleModeChange}>
          <option value="existing">Bestaand spel gebruiken</option>
          <option value="api">Spel starten via de API</option>
        </select>
      </div>

      {mode === "existing" ? (
        <>
          <input
            type="text"
            placeholder="Geef hier een gameKey in"
            onChange={handleInputChange}
          />

          {!existingGameKey && (
            <div>
              <div>
                <i
                  className={"fas fa-exclamation-triangle"}
                  style={{ marginRight: "10px" }}
                />
                Geen gamekey ingegeven.
              </div>
            </div>
          )}

          {existingGameKey && (
            <div className="cluedo-start" onClick={handleExistingGame}>
              <div className="cluedo-start-img"></div>
              <p>Nieuw Spel</p>
            </div>
          )}
        </>
      ) : (
        <>
          {gameKey.error && (
            <div>
              <div>
                <i
                  className={"fas fa-exclamation-triangle"}
                  style={{ marginRight: "10px" }}
                />
                Geen spel data gevonden.
              </div>
              Error: {gameKey.error}
            </div>
          )}
          {gameKey.processing && <div>Loading...</div>}

          {!gameKey.processing && (
            <div className="cluedo-start" onClick={handleNewGame}>
              <div className="cluedo-start-img"></div>
              <p>Nieuw Spel</p>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default CluedoStart;
